/**
 * Global unique identifier.
 * @see https://stackoverflow.com/a/105074/1786459
 * @return {String}
 */
export default function guid() {
  function s4() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }
  return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
    s4() + '-' + s4() + s4() + s4();
}



